import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
@Component({
  selector: 'app-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.scss']
})
export class ViewComponent implements OnInit {

  public items;
  public c59TO;
  public c60TO;
  public c61TO;
  public name;
  public c60;
  constructor(public db: AngularFireDatabase, public router: Router) {

    this.items = db.list('STUDENT').valueChanges();

    db.list('STUDENT', ref => ref.orderByChild('AcademicYear').equalTo('2560')).valueChanges().subscribe(data => {
      console.log(data[1]['AcademicYear']);
      for (let i = 1; i <= data.length; i++ ) {
        console.log(data[i]['Course']);
        if (data[i]['Course'] === 'ปกติ') {
          console.log(data[i]['Course']);
          this.c60 = data[i];
        }
      }
    });

    this.c60 = db.list('STUDENT', ref => ref.orderByChild('AcademicYear').equalTo('2560')).valueChanges();
    this.c59TO = db.list('STUDENT', ref => ref.orderByChild('AcademicYear').equalTo('2559')).valueChanges();

    this.c60TO = db.list('STUDENT', ref => ref.orderByChild('AcademicYear').equalTo('2560')).valueChanges();
    this.c61TO = db.list('STUDENT', ref => ref.orderByChild('AcademicYear').equalTo('2561')).valueChanges();
   }

  ngOnInit() {
  }
  click(ID: string | number | boolean) {
    this.name = this.db.list('STUDENT', ref => ref.orderByChild('ID').equalTo(ID)).valueChanges();
  }
  cLink(id: any) {
    this.router.navigate(['/profile', id]);
  }
}
